















import { Vue, Component, Prop, Watch, Ref } from "vue-property-decorator";
import { Task, schedule } from "./include";

function line(
  ctx: CanvasRenderingContext2D,
  x1: number,
  y1: number,
  x2: number,
  y2: number
) {
  ctx.beginPath();
  ctx.moveTo(x1, y1);
  ctx.lineTo(x2, y2);
  ctx.stroke();
}

const x0 = 30;
const y0 = 0;
const tmax = 1000;
const h = 110;
const k = 1.5;

@Component
export default class Viz extends Vue {
  @Ref("canvas") canvas!: HTMLCanvasElement;

  async mounted() {
    this.$nextTick();
    this.canvas.height = window.innerHeight;
    this.canvas.width = Math.floor(this.canvas.parentElement!.clientWidth - 5);
    this.onTasksChange();
  }

  @Prop() edf!: boolean;

  guides: number[] = [];

  x2t(x: number) {
    return (x - this.canvas.offsetLeft - x0) / k;
  }

  t2xc(t: number) {
    return t * k + x0;
  }

  onClick(ts: number[]) {
    return (e: MouseEvent) => {
      if (e.shiftKey) {
        this.guides = [];
        return;
      }
      const t0 = this.x2t(e.offsetX);
      console.log(t0);
      type X = {
        t: number;
        dist: number;
      };
      const ans = ts.reduce(
        (x: X, tt) =>
          Math.abs(tt - t0) < x.dist ? { dist: Math.abs(tt - t0), t: tt } : x,
        { t: 0, dist: Number.POSITIVE_INFINITY }
      );
      this.toggleGuide(ans.t);
    };
  }

  toggleGuide(t: number) {
    const i = this.guides.findIndex(tt => t === tt);
    if (i >= 0) {
      this.guides.splice(i, 1);
    } else {
      this.guides.push(t);
    }
  }

  listener: (e: MouseEvent) => void = () => 0;

  @Prop() tasks!: Task[];
  @Watch("tasks", { deep: true })
  @Watch("edf")
  onTasksChange() {
    console.log("tc");

    const canvas = this.canvas;
    const tasks = this.tasks;
    const n = tasks.length;
    if (!n || !tasks.every(t => t.t > 0)) return;
    const ctx = this.canvas.getContext("2d")!;
    const { width, height } = ctx.canvas;
    ctx.clearRect(0, 0, width, height);

    function drawInterval(i: number, beg: number, end: number) {
      ctx.fillRect(x0 + k * beg, y0 + (i + 0.5) * h, k * (end - beg), 0.5 * h);
    }

    for (const [a, b, c, r] of schedule(tasks, tmax, this.edf)) {
      ctx.fillStyle = "#51bbfe";
      drawInterval(a, b, c);
      if (!r) {
        ctx.fillStyle = "green";
        ctx.beginPath();
        ctx.ellipse(x0 + k * c, y0 + (a + 0.5) * h, 5, 5, 0, 0, Math.PI * 2);
        ctx.fill();
      }
    }

    const ts: Set<number> = new Set();

    for (let i = 0; i < n; ++i) {
      for (let t = tasks[i].i; t <= tmax; t += tasks[i].t) {
        line(ctx, x0, y0 + i * h + h, x0 + tmax * k, y0 + i * h + h);
        const x1 = x0 + k * t;
        const y1 = y0 + (1 + i) * h;
        line(ctx, x1, y1, x1, y1 - 0.75 * h);
        ts.add(t);
      }
    }
    canvas.removeEventListener("click", this.listener);
    const f = (this.listener = this.onClick([...ts]));
    canvas.addEventListener("click", f);
  }
}
