const k = 1.5;
const h = 15;

export type Task = {
  t: number;
  c: number;
  i: number;
};

export function* schedule(tasks: Task[], tmax: number, edf = false) {
  const n = tasks.length;
  if (!n) return;
  const nextRequests = tasks.map(t => t.i);
  const remain = Array(n).fill(0);
  let t = 0;
  while (t < tmax) {
    let i;
    if (!edf) {
      i = remain.findIndex(v => v > 0);
    } else {
      const indices = remain
        .map((v, i) => ({ v, i }))
        .filter(({ v }) => v > 0)
        .map(({ i }) => i);
      i = !indices.length
        ? -1
        : indices.reduce((x, i) => (nextRequests[i] < nextRequests[x] ? i : x));
    }
    if (i >= 0) {
      const closestRequest = Math.min(...nextRequests);
      const taskEnd = t + remain[i];
      let tend = 0;
      if (taskEnd <= closestRequest) {
        remain[i] = 0;
        tend = taskEnd;
      } else {
        remain[i] -= closestRequest - t;
        tend = closestRequest;
      }
      yield [i, t, tend, remain[i]];
      t = tend;
    } else {
      t = Math.min(...nextRequests);
    }
    for (let i = 0; i < n; ++i) {
      if (nextRequests[i] === t) {
        if (remain[i]) {
          console.log("overflow");
          return;
        }
        nextRequests[i] += tasks[i].t;
        remain[i] = tasks[i].c;
      }
    }
  }
}
