














































































import { Vue, Component, Prop } from "vue-property-decorator";
import Viz from "./Viz.vue";
import { Task } from "./include";

function encode(tasks: Task[]): string {
  return tasks.map(({ i, c, t }) => `${t}_${c}_${i}`).join("'");
}

function decode(s: string): Task[] {
  return s.split("'").map(st => {
    const [t, c, i] = st.split("_").map(Number.parseFloat);
    return { t, c, i };
  });
}

@Component({
  components: { Viz }
})
export default class App extends Vue {
  mode: "edf" | "fixed" = "fixed";
  created() {
    if (!window.location.hash) {
      return;
    }
    this.tasks = decode(window.location.hash.slice(1));
  }
  tasks: Task[] = [
    { c: 10, t: 50, i: 0 },
    { c: 10, t: 60, i: 0 },
    { c: 10, t: 70, i: 0 },
    { c: 10, t: 80, i: 0 }
  ];
  tset(e: InputEvent, i: number, k: "i" | "c" | "t") {
    const target = e.target as HTMLInputElement;
    const v = +target.value;
    if (Number.isNaN(v) || v < 0) {
      e.preventDefault();
      return;
    }
    this.tasks[i][k] = v;
  }
  add() {
    this.tasks.push({ c: 0, t: 100, i: 0 });
  }
  remove() {
    this.tasks.pop();
  }
  get url() {
    return window.location.href.split("#")[0] + "#" + encode(this.tasks);
  }
}
